
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import nullData from "@/components/nullData.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

export default {
name: "subStoreList",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading,
nullData
},
data() {
return {
fanShow_bottom: false,
loading_bottom: false,
fanList:[],
list_page:1,
};
},
created() {
this.getFansList();

},
mounted(){
this.$nextTick(function() {
isScrollBottom(this.scrollBottomCB);
});
},
methods: {
async getFansList(){

let data={
account:this.account,
token:this.token,
plat:3,
page:this.list_page,
}
this.list_page++;
const [err, res] = await api.storefanList(data);//子店铺粉丝列表
if (err) {
console.log("err", err);
return;
}
if (res && res.code == "2000") {
this.fanList = this.fanList.concat(res.data.list);
}
},


scrollBottomCB() {
console.log('滚动到底部',this.list_page)
this.getFansList();
}
},
filters: {},
computed: {
...mapGetters(["user", "account", "token"])
},

};



